html {
  box-sizing: border-box;
  margin: 0;
  background-attachment: fixed;
  /* background-image: url(./serveimage.jpeg); */
  background-size: cover;
  height: 100vh;
  width: 100vw;
}

.app-header {
  text-align: center;
  width: 100%;
  color: rgb(0, 49, 128);
}

.add-form {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgba(35, 71, 129, 0.7);
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: rgb(35, 71, 129, 0.7);
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: rgb(35, 71, 129, 0.7);
}

.add-form .add-btn {
  width: 300px;
  height: 25px;
  font-size: 16px;
  border-radius: 3px;
  background-color: white;
  color: rgb(0, 49, 128);
  border: 2px solid rgb(0, 49, 128);
  padding: 15px 32px;
  margin-bottom: 0;
}

.datamuse-res {
  margin-top: 0;
  list-style: none;
  text-align: left;
  width: 300px;
  font-size: 16px;
  color: rgb(0, 49, 128);
  border: 2px solid rgb(0, 49, 128);
  border-top: none;
  border-bottom: none;
  cursor: pointer;
  border-radius: 3px;
  padding: 0;
}

.datamuse-res li {
  padding: 10px;
  border-bottom: 2px solid rgb(0, 49, 128);
}

.datamuse-res li:hover {
  background: rgb(223, 235, 255);
}

.add-form button {
  display: block;
  background-color: rgb(0, 49, 128);
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  border-radius: 3px;
  cursor: pointer;
  margin: 15px;
}

.add-form button:hover {
  background-color: rgb(80, 127, 204);
}

.sort-container {
  color: white;
  background-color: rgb(0, 49, 128);
  width: 60vw;
  margin: 0 auto;
  padding: 8px;
  border-radius: 3px;
  text-align: end;
}

.link {
  text-decoration-style: solid;
  color: white;
  margin: 5px;
}

.search-input {
  color: rgb(0, 49, 128);
  border: 1px solid rgb(0, 49, 128);
  border-radius: 3px;
  width: 0px;
  transition: width 0.3s;
}

.search-input.active {
  width: 150px;
}

.list button {
  display: inline-block;
  background-color: rgb(0, 49, 128);
  border: none;
  color: white;
  padding: 5px 35px;
  height: 40px;
  text-align: center;
  text-decoration: none;
  font-size: 14px;
  border-radius: 3px;
  cursor: pointer;
  margin: 2.5px;
  justify-self: center;
  align-self: center;
}

.novocy {
  text-align: center;
  margin: 0 auto;
}

.list h2 {
  font-weight: 500;
}

button:hover {
  background-color: rgb(80, 127, 204);
}

.list .del-btn {
  background-color: rgb(128, 0, 0);
}

.list .hl-btn {
  background-color: rgb(255, 217, 0);
  color: black;
}

.list .hl-btn:hover {
  background-color: rgb(251, 232, 119);
}

.list .del-btn:hover {
  background-color: rgb(195, 71, 71);
}

.list {
  width: 60vw;
  margin: 0 auto;
}

.list-item {
  display: grid;
  grid-template-columns: auto 3fr auto auto auto;
  margin: 0 5px;
  padding: 5px 2vw 5px 10px;
}

.list-item:hover {
  background-color: rgba(218, 218, 218, 0.5);
}

.list-item span {
  padding: 6px 15px;
  border-radius: 3px;
  cursor: pointer;
}

.list-item h2 {
  grid-column-start: 2;
  margin: 15px;
}

.list-item form {
  grid-column-start: 3;
}

.toggle-mod-input {
  width: 150px;
  height: 12px;
  font-size: 14px;
  border-radius: 3px;
  background-color: white;
  color: rgb(0, 49, 128);
  border: 2px solid rgb(0, 49, 128);
  padding: 12px 15px;
  margin: 15px;
}

.dwn-btn {
  position: fixed;
  background-color: rgb(0, 49, 128);
  border: none;
  color: white;
  border-radius: 50%;
  cursor: pointer;
  height: 40px;
  width: 40px;
  right: 5px;
  bottom: 5px;
}

.dwn-text {
  position: fixed;
  right: -200px;
  bottom: 5px;
  background-color: rgb(0, 49, 128);
  color: white;
  border-radius: 20px;
  padding: 10px 20px;
  opacity: 0;
  transition: all 0.3s;
}

.dwn-text.hover {
  right: 55px;
  opacity: 1;
}

.def-container {
  border: 2px solid rgb(0, 49, 128);
  border-radius: 5px;
  padding: 10px;
  background: rgb(223, 235, 255);
}

footer {
  position: fixed;
  font-size: 12px;
  text-decoration: none;
  color: rgb(0, 49, 128);
  bottom: 0;
}
.list .show-def {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  background-color: white;
  color: rgb(0, 49, 128);
  justify-self: center;
  align-self: center;
  padding: 5px;
  border: 1px solid rgb(0, 49, 128);
  opacity: 0;
  pointer-events: none;
}

.list .show-def:hover {
  color: white;
  background: rgb(0, 49, 128);
}

.list .phon {
  font-size: 16px;
  font-weight: 400;
  padding-left: 5;
}

@media only screen and (max-width: 850px) {
  /* For mobile phones: */

  .list .phon {
    font-size: 12px;
  }

  footer {
    font-size: 6px;
  }

  .list .show-def {
    font-size: 14px;
    width: 20px;
    height: 20px;
    padding: 0;
    margin: 0;
    opacity: 1;
    pointer-events: initial;
  }

  .app-header {
    font-size: 12px;
    font-weight: 900;
  }

  .add-form input {
    padding: 7px;
    margin: 5px 10px;
  }

  .add-form button {
    padding: 10px;
    margin: 5px;
  }

  .list {
    width: 100%;
    padding-top: 10px;
  }

  .list-item {
    grid-template-columns: 30px auto 60px 60px 60px;
    height: 30px;
  }

  .list-item h2 {
    font-size: 16px;
    padding: 7px 0px;
    margin: 0;
  }

  .list-item button {
    font-size: 12px;
    padding: 0;
    height: 25px;
    margin: 0 3px;
    justify-self: stretch;
  }

  .sort-container {
    width: 95vw;
    font-size: 14px;
    margin-right: 15px 0;
  }

  .search-input.active {
    width: 90px;
  }
}
